import '../../../libs/slick/slick.min';
import '../../../libs/popup/jquery.magnific-popup.min';
import countries from "../../../data/countries";

$(document).ready(function () {

  // line

  const target = document.querySelector(".line");
  const links = document.querySelectorAll(".nav-list__link_main");


  for (let i = 0; i < links.length; i++) {
    links[i].addEventListener("mouseenter", mouseenterFunc);
  }

  function mouseenterFunc() {
    for (let i = 0; i < links.length; i++) {
      if (links[i].parentNode.classList.contains("active")) {
        links[i].parentNode.classList.remove("active");
      }
    }

    this.parentNode.classList.add("active");

    const width = this.getBoundingClientRect().width;
    // const height = this.getBoundingClientRect().height;
    const left = this.getBoundingClientRect().left;
    // const top = this.getBoundingClientRect().top;

    target.style.width = `${width}px`;
    // target.style.height = `${height}px`;
    target.style.left = `${left}px`;
    // target.style.top = `${top}px`;
    target.style.borderColor = 'red';
    target.style.transform = "none";
  }

  // init controller
  var controller = new ScrollMagic.Controller();

  new ScrollMagic.Scene({triggerElement: "#trigger", offset: "330", duration: ""})
    .setClassToggle("#header", "active")
    .addTo(controller);

  var controller2 = new ScrollMagic.Controller();
  var tween2 = TweenMax.to("#cloud", 0.5, {left: "100%", top: "500px", ease: Linear.easeNone});
  new ScrollMagic.Scene({triggerElement: "#trigger", duration: 900, offset: -600})
    .setTween(tween2)
    .addTo(controller2);

  var controller3 = new ScrollMagic.Controller();
  var tween3 = TweenMax.to("#cloud2", 0.5, {right: "100%", bottom: "-50px", ease: Linear.easeNone});
  new ScrollMagic.Scene({triggerElement: "#trigger2", duration: 900, offset: 300})
    .setTween(tween3)
    // .addIndicators({name: "cloud"})
    .addTo(controller3);
  //

  $('.team-slider').slick({
    dots: true,
    arrows: false,
    infinite: true,
    speed: 600,
    autoplay: false,
    autoplaySpeed: 2000,
    slidesToShow: 3,
    slidesToScroll: 1,
  });

  $('.boat-slider').slick({
    dots: false,
    arrows: false,
    infinite: true,
    speed: 600,
    autoplay: false,
    autoplaySpeed: 2000,
    slidesToShow: 5,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1365,
        settings: {
          centerMode: true,
          centerPadding: '60px',
          slidesToShow: 3,
          slidesToScroll: 1,
        }
      },
    ]
  });

  $('.slider-for').slick({
    slidesToShow: 3,
    slidesToScroll: 1,
    arrows: false,
    asNavFor: '.slider-nav',
    speed: 600,
  });

  $('.slider-nav').slick({
    slidesToShow: 3,
    slidesToScroll: 1,
    asNavFor: '.slider-for',
    dots: false,
    arrows: false,
    focusOnSelect: true,
    speed: 600,
    autoplay: false,
    autoplaySpeed: 2000,
  });

  $('.circle_prev').on('click', function () {
    $('.slider-nav').slick('slickPrev')
  });

  $('.circle_next').on('click', function () {
    $('.slider-nav').slick('slickNext')
  });

  //popup

  $('.popup-youtube').magnificPopup({
    disableOn: 700,
    type: 'iframe',
    fixedContentPos: false,
    fixedBgPos: true,
    overflowY: 'auto',
    closeBtnInside: true,
    preloader: false,
    midClick: true,
    removalDelay: 300,
    mainClass: 'my-mfp-zoom-in'
  });

  // Search

  const input = document.querySelector("#search-field");
  const list = document.querySelector("#search-list");

  input.addEventListener("keyup", complete);
  list.addEventListener("click", selectWord);

  function complete() {
    const val = input.value.trim().toLocaleLowerCase();
    list.style.display = "block";
    if(val.length > 1) {
      const country = countries.filter(county => county.name.toLocaleLowerCase().indexOf(val) === 0);

      if(country.length >= 1) {
        list.innerHTML = renderHtml(country);
      } else {
        list.innerHTML = `<li class="form-select__item"><span>No results...</span></li>`;
      }
    } else {
      list.innerHTML = `<li class="form-select__item"><span>The list is too short...</span></li>`;
    }
  }

  function selectWord(e) {
    e.preventDefault();
    const target = e.target;
    if(target.parentNode.nodeName === "LI") {
      input.value = target.innerHTML;
      list.style.display = "none";
    }
  }

  function renderHtml(arr) {
    let html = "";
    for (let i = 0; i < arr.length; i++) {
      html += `<li class="form-select__item"><a href="#">${ arr[i].name }</a></li>`
    }

    return html;
  }

  $(document).on('click', function (e) {
    if(!$(e.target).closest(".search").length) {
      $("#search-list").css("display", "none");
    }
    e.stopPropagation();
  });
});